/*-----File-manager-----*/

.file-sm {
    font-size: 12px;
    padding: 4px 12px;
}

.file-md {
    font-size: 15px;
    padding: 4px 13px;
}

.file-lg {
    font-size: 17px;
    padding: 6px 15px;
}

.file-image img {
    border-radius: 7px;
}

.file-image .icons {
    padding: 0;
    margin: 0;
    list-style: none;
    opacity: 0;
    inset-inline-end: 22px;
    inset-block-start: 14px;
    text-align: center;
    position: absolute;
    inset-block-end: -3px;
    z-index: 1;
    transition: all .5s ease 0s;
    text-align: end;
}

.file-image .icons li {
    display: block;
    margin: 5px;
}

.file-image .icons .btn {
    color: $white;
}

.file-image .icons .btn {
    font-size: 15px;
    line-height: 35px;
    text-align: center;
    height: 35px;
    width: 35px;
    margin: 2px 7px;
    padding: 0;
    border-radius: 50px;
    display: block;
    position: relative;
    transition: all .3s ease-in-out;
    min-width: inherit;
}

.file-image .icons .btn:after {
    content: '';
    height: 15px;
    width: 15px;
    border-radius: 0;
    transform: translateX(-50%) rotate( 45deg);
    inset-block-start: -20px;
    z-index: -1;
}

.file-image:hover {
    border-radius: 7px;
}

.file-image:hover .icons {
    opacity: 1;
    inset-block-end: 33px;
}

.file-image .icons .btn:hover {
    color: $white;
}

.file-name {
    position: absolute;
    inset-block-end: 20px;
    inset-inline-start: 0;
    inset-inline-end: 0;
    color: $white;
    font-size: 16px;
    text-align: center;
    font-weight: 500;
}

.file-manager-image {
    background: #e5bc4a40;
    border-radius: 50px;
    margin-block-end: 23px;
    padding: 6px;
    width: 60px;
    height: 60px;
}

.open-file {
    position: absolute;
    height: 100%;
    width: 100%;
    z-index: 1;
    inset-inline-end: 0;
    inset-inline-start: 0;
    inset-block-start: 0;
}

.file-dropdown {
    z-index: 2;
}

.tag-outline {
    border: 1px solid #e8e8f7;
    color: #1d212f;
    background-color: transparent;
}

@media (max-width: 576px) {
    .file-lg {
        padding: 6px 4px;
    }
}

.file-image-1{
    width: 100px;
    height: 100px;
    display: inline-table;
    margin: 4px;
    position: relative;
    border: 1px solid $border;
    border-radius: 5px;
    margin-block-end: 20px;
}
.file-image-1 .product-image img {
    height: 100%;
    background-size: cover;
    background-repeat: no-repeat;
  }
.file-image-1 .icons{
    padding: 0;
    margin: 0;
    list-style: none;
    opacity: 0;
    inset-inline-end: 7px;
    inset-block-start: 6px;
    text-align: center;
    position: absolute;
    inset-block-end: 0;
    z-index: 1;
    transition: all 0.5s ease 0s;
    text-align: end;

}
.file-image-1:hover .icons {
    opacity: 1;
    inset-block-end: 33px;
}
.file-image-1 .icons .btn {
    font-size: 12px;
    line-height: 26px;
    text-align: center;
    height: 25px;
    width: 25px;
    margin: 4px 1px;
    padding: 0;
    border-radius: 50px;
    display: block;
    position: relative;
    transition: all 0.3s ease-in-out;
    color: $white;
    min-width: inherit;
}
.file-image-1 .file-name-1{
    position: absolute;
    inset-inline-end: 0;
    inset-inline-start: 0;
    inset-block-end: -22px;
    color: #524d4d;
    font-size: 12px;
    text-align: center;
    font-weight: 500;
}


//File image-2 styles

.file-image-md{
    width: 150px;
