}

//File image-2 styles

.file-image-lg{
    width: 200px;
    height: 200px;
}
.file-image-lg .icons .btn {
    font-size: 15px;
    line-height: 35px;
    height: 35px;
    width: 35px;
}
.card.pos-relative{
    .card-body{
        .bg-primary-transparent{
            path{
                fill: $primary;
            }
        }
    }
}



.btn-group.file-attach .btn-close {
    padding: 9px;
    border-start-start-radius: 0px;
    border-start-end-radius: 4px;
    border-end-end-radius: 4px;
    border-end-start-radius: 0px;
    opacity: 1;
    font-weight: 300;
    background-image: none;
	&.btn-pill {
        border-start-start-radius: 0px;
        border-start-end-radius: 50px;
        border-end-end-radius: 50px;
        border-end-start-radius: 0px;
	}
	&.btn-outline-secondary {
		border: 1px solid $secondary !important;
		color: $secondary !important;
	}
	&.btn-outline-secondary:hover {
		color: $white !important;
	}
	&.btn-outline-primary {
		border: 1px solid $primary;
		color: $primary;
	}
	&.btn-outline-primary:hover {
		color: $white !important;
		border-color: $primary !important;
	}
	&.btn-outline-info {
		border: 1px solid $azure !important;
		color: $azure !important;
	}
	&.btn-outline-info:hover {
		color: $white !important;
	}
	&.btn-outline-warning {

		border: 1px solid $warning !important;
		color: $warning !important;
	}
	&.btn-outline-warning:hover {
		color: $white !important;
	}
	&.btn-outline-danger {
		border: 1px solid $danger !important;
		color: $danger !important;
	}
	&.btn-outline-danger:hover {
		color: $white !important;
	}
	&.btn-outline-success {
		border: 1px solid $success !important;
		color: $success !important;
	}
	&.btn-outline-success:hover {
		color: $white !important;
	}
}
.btn-group.file-attach .btn-close.btn-lg {
    padding: 10px;
    background-image: none;
    font-size: 31px;
}
.btn-group.file-attach .btn-close.btn-sm {
    font-size: 1rem !important;
    background-image: none;
}
.btn-group.file-attach .btn-sm {
    font-size: 12px !important;
}
.btn-group.file-attach{
    button.btn-close{
        inset-inline-end: 0;
    }
	.btn-primary-light {
		border: 0 !important;
	}
    .btn-pill.btn-close{
        padding-inline-start: 2px;
    }
    .btn{
        display: flex;
        align-items: center;
        justify-content: center;
    }
     .btn+.btn.btn-primary-light, .btn+.btn-group {
        margin-inline-start: 0px;
        color: $primary;
    }
}
