/*------ Default-menu---------*/

@media (min-width: 992px) {
    .app.sidenav-toggled {
        .app-content {
            margin-inline-start: 0;
        }
        .app-sidebar {
            inset-inline-start: 230px;
        }
        .app-sidebar__overlay {
            visibility: hidden;
        }
    }
    .app-sidebar {
        padding-block-start: 55px;
    }
    .side-header {
        width: 270px;
        inset-inline-start: 0;
        inset-inline-end: 0;
        inset-block-start: 0;
        position: fixed;
        z-index: 99999999;
        background: $white;
    }
    .app.sidebar-mini.sidenav-toggled .side-header {
        width: 80px !important;
    }
    .sidebar-mini footer.footer {
            padding-block-start: 1.25rem;
            padding-inline-end: 1.25rem;
            padding-block-end: 1.25rem;
            padding-inline-start: 270px;
    }
    .sidebar-mini.sidenav-toggled footer.footer {
        padding: 1.25rem 1.25rem 1.25rem 80px;
    }
}

@media (max-width: 991px) {
    .app {
        overflow-x: hidden;
        .app-sidebar {
            inset-inline-start: -270px;
            inset-block-start: 68px;
        }
        .app-sidebar__overlay {
            visibility: hidden;
        }
        &.sidenav-toggled {
            .app-content {
                margin-inline-start: 0;
            }
            .app-sidebar {
                inset-inline-start: 0;
            }
            .app-sidebar__overlay {
                visibility: visible;
            }
        }
        &.sidebar-mini.sidebar-gone.sidenav-toggled .app-sidebar {
            inset-inline-start: 0;
        }
        .app-header .header-brand1 {
            width: inherit;
            position: absolute;
            inset-inline-start: 0;
            inset-inline-end: 0;
        }
    }
}

.app-content {
    min-height: calc(88vh - 50px);
    margin-block-end: 0 !important;
    -webkit-transition: margin-inline-start 450ms ease;
    -o-transition: margin-inline-start 450ms ease;
    transition: margin-inline-start 450ms ease;
    overflow: clip;
    .side-app {
        padding: 0px 20px 0 20px;
    }
}

.footer .container {
    width: 100%;
    padding-inline-end: 0.75rem;
    padding-inline-start: 0.75rem;
    margin-inline-end: auto;
    margin-inline-start: auto;
}

@media (min-width: 992px) {
    .app-content {
        margin-inline-start: 270px;
    }
}

@media (max-width: 991px) {
    .app-content {
        min-width: 100%;
    }
}

@media (max-width: 480px) {}

@media print {
    .app-content {
        margin: 0;
        padding: 0;
        background-color: $white;
    }
}

.app-header {
    position: fixed;
    inset-inline-start: 0;
    inset-inline-end: 0;
    inset-block-start: 0;
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    z-index: 9999 !important;
    border-block-end: 1px solid $border;
    background: $white;
    transition: padding-inline-start 0.3s ease;
}

@media (min-width: 992px) {
    .app-header {
        padding-inline-end: 10px;
        padding-inline-start: 285px;
        z-index: 99 !important;
    }
}

@media print {
    .app-header {
        display: none;
    }
}

.app-header__logo {
    -webkit-box-flex: 1;
    -ms-flex: 1 0 auto;
    flex: 1 0 auto;
    color: $white;
    text-align: center;
    font-family: 'Niconne';
    padding: 0 15px;
    font-size: 26px;
    font-weight: 400;
    line-height: 50px;
    &:focus,
    &:hover {
        text-decoration: none;
    }
}

@media (min-width: 991px) {
    .app-header__logo {
        -webkit-box-flex: 0;
        -ms-flex: 0 0 auto;
        flex: 0 0 auto;
        display: block;
        width: 230px;
    }
}

.app-sidebar__toggle {
    color: $muted;
    z-index: 1;
    transition: padding-inline-start 0.3s ease;
    padding: 0 9px;
    text-align: center;
    font-size: 1.5rem;
    position: relative;
    inset-inline-end: 7px;
    &:before {
        content: "\e907";
        transition: all .25s;
        font-size: 21px;
        font-family: 'feather';
    }
    &:after {
        content: '';
        position: absolute;
        width: 16px;
        height: 16px;
        inset-block-start: 12px;
        inset-block-end: 0;
        border-radius: 50%;
        inset-inline-end: 12px;
    }
    &:focus,
    &:hover {
        text-decoration: none;
    }
}

@media (max-width: 991px) {
    .app-sidebar__toggle {
        -webkit-box-ordinal-group: 0;
        -ms-flex-order: -1;
        order: -1;
    }
}


.app-sidebar {
    position: fixed;
    inset-block-start: 0;
    inset-block-end: 0;
    inset-inline-start: 0;
    color: $default-color;
    z-index: 1024;
    background: $white;
    -webkit-box-shadow: 0 8px 24px rgba(168, 180, 208, .1);
    box-shadow: 0 8px 24px rgba(168, 180, 208, .1);
    -webkit-transition: left 450ms ease, width 450ms ease;
    transition: left 450ms ease, width 450ms ease;
    -o-transition: left 450ms ease, width 450ms ease;
    border-inline-end: 1px solid $border;
    &::-webkit-scrollbar {
        width: 6px;
    }
    &::-webkit-scrollbar-thumb {
        background: $black-2;
    }
}

@media print {
    .app-sidebar {
        display: none;
    }
}

@media (max-width: 991px) {
    .app-sidebar__overlay {
        position: fixed;
        inset-block-start: 0;
        inset-inline-start: 0;
        inset-block-end: 0;
        inset-inline-end: 0;
        z-index: 9;
    }
}

.app-sidebar__user {
    .dropdown-menu {
        inset-block-start: 10px !important;
    }
    img {
        box-shadow: 0 0 25px $white-1;
        padding: 2px;
        border: 1px solid $white-2;
        box-shadow: 0px 5px 5px 0px rgba(44, 44, 44, 0.2);
    }
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    color: #a8a8a8;
    width: 100%;
    padding: 15px 0 15px;
    display: inline-block;
}

.app-sidebar__user-avatar {
    -webkit-box-flex: 0;
    -ms-flex: 0 0 auto;
    flex: 0 0 auto;
    margin-inline-end: 15px;
}

.app-sidebar__user-name {
    &.text-sm {
        font-size: 12px;
        font-weight: 400;
    }
    font-size: 17px;
    line-height: 1.3;
    white-space: nowrap;
    overflow: hidden;
    -o-text-overflow: ellipsis;
    text-overflow: ellipsis;
    margin-block-end: 0;
    overflow: hidden;
    font-weight: 600;
    color: #e5e9ec;
    font-size: 15px;
    margin-block-start: 5px !important;
}

.app-sidebar__user-designation {
    white-space: nowrap;
    overflow: hidden;
    -o-text-overflow: ellipsis;
    text-overflow: ellipsis;
    margin-block-end: 0;
    overflow: hidden;
    font-weight: 600;
    color: #e5e9ec;
    font-size: 15px;
    margin-block-start: 5px !important;
}

.side-menu {
    margin-block-end: 0;
    padding: 0 10px 0 10px;
}

.side-menu__item {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding: 8px 20px;
    font-size: 14px;
    font-weight: 400;
    -webkit-transition: border-inline-start-color 0.3s ease, background-color 0.3s ease;
    -o-transition: border-inline-start-color 0.3s ease, background-color 0.3s ease;
    transition: border-inline-start-color 0.3s ease, background-color 0.3s ease;
    color: #76839a;
    margin: 0px 0px;
    white-space: nowrap;
    &.active {
        text-decoration: none;
        color: $primary;
        &:hover,
        &:focus {
            text-decoration: none;
            color: $primary;
        }
    }
    &:hover,
    &:focus {
        text-decoration: none;
        color: $primary;
        .side-menu__icon,
        .side-menu__label {
            color: $primary;
        }
    }
}

@media (min-width:992px) {
    .app.sidebar-mini.sidenav-toggled .side-menu__label {
        display: none !important;
    }
}

.slide-item {
    &.active,
    &:hover,
    &:focus {
        text-decoration: none;
        color: #b5c1d2;
    }
}

.slide-menu a.active {
    color: $primary;
}

.slide-item {
    &.active,
    &:hover,
    &:focus {
        text-decoration: none;
        color: $primary !important;
    }
}

.slide-menu li {
    .slide-item {
        &:before {
            content: '\e0bc';
            font-family: 'typicons';
            font-style: normal;
            font-weight: normal;
            text-decoration: inherit;
            inset-block-start: 0px;
            font-size: 10px;
            margin-inline-end: 7px;
            color: #68798b;
        }
    }
    position: relative;
}

.slide.is-expanded a {
    text-decoration: none;
}

.side-menu .side-menu__icon {
    color: $muted;
    font-size: 16px;
    line-height: 30px;
    text-align: center;
    vertical-align: middle;
    margin-inline-end: 8px;
    position: relative;
}

.side-menu__icon {
    -webkit-box-flex: 0;
    -ms-flex: 0 0 auto;
    flex: 0 0 auto;
}

.side-menu__label {
    white-space: nowrap;
    -webkit-box-flex: 1;
    -ms-flex: 1 1 auto;
    flex: 1 1 auto;
    margin-block-start: 3px;
}

.slide.is-expanded {
    .slide-menu {
        display: block;
    }
    .angle {
        -webkit-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
        transform: rotate(90deg);
    }
}

.slide-menu {
    display: none;
    font-size: .7rem !important;
    padding-inline-start: 30px;
}

.slide-item {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding: 8px 8px;
    font-size: 13.5px;
    color: $muted;
    white-space: nowrap;
    .icon {
        margin-inline-end: 5px;
    }
}

.angle {
    -webkit-transform-origin: center;
    -ms-transform-origin: center;
    transform-origin: center;
    -webkit-transition: -webkit-transform 0.3s ease;
    transition: -webkit-transform 0.3s ease;
    -o-transition: transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
    font-size: 14px;
}

@media (min-width: 992px) {
    .sidebar-mini.sidenav-toggled {
