        }
        .slide.is-expanded .slide-menu {
            display: block !important;
            position: unset;
            inset-block-start: inherit;
            min-width: 0;
            width: 100%;
            opacity: inherit;
            visibility: inherit;
        }
        .side-menu {
            margin-block-start: 20px !important;
            padding: 0 10px 0 10px !important
        }
